const initialState = {
  visibleState: false,
  text: '',
  toastColor: 'success',
  textColor: 'white',
}

const testState = (state = initialState, {type, ...rest}) => {
  switch (type) {
    case 'set':
      return {...state, ...rest}
    default:
      return state
  }
}

export default testState
